import React, { Component } from "react";
import CategoryButtonGroup from "./categorybuttongroup";
import firebase from "./config/config.jsx";
// import firebase from "firebase/app";
// import "firebase/database";
import {
  Grid,
  Row,
  Col,
  ListGroup,
  ListGroupItem,
  Badge
} from "react-bootstrap";
import ListComponent from "./listcomponent";
import ContentProducts from "./content";
import Jumbotron from "./jumbotron";
import "./category.css";

class Category extends Component {
  state = {
    data: [],
    category: ""
  };

  constructor() {
    super();
    this.ref = firebase.firestore().collection("Category");
    this.unsubscribe = null;
  }

  onCollectionUpdate = querySnapshot => {
    const data = this.state.data;
    querySnapshot.forEach(doc => {
      const { Title } = doc.data();
      data.push({
        key: doc.id,
        doc, // DocumentSnapshot
        Title
      });
    });
    this.setState({
      data: data
    });
  };

  componentWillMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
  }

  render() {
    if (this.state.category === "") {
      return (
        <div ref="destroy">
          <Grid>
            <Row>
              <Jumbotron />
            </Row>
            <h1 className="title">
              <u>Categories</u>
              <Badge className="badge-margin">{this.state.data.length}</Badge>
            </h1>
          </Grid>

          <CategoryButtonGroup className="for-margin" />
          <Grid className="grid-margin">
            <Row>
              <Col sm={12} md={4}>
                <ListGroup>
                  {this.state.data.map((c, index) => {
                    if (index % 3 == 0)
                      return (
                        <ListGroupItem
                          key={index}
                          onClick={() => {
                            this.setState({ category: c.Title });
                          }}
                        >
                          {c.Title}
                        </ListGroupItem>
                      );
                  })}
                </ListGroup>
              </Col>
              <Col sm={12} md={4}>
                <ListGroup>
                  {this.state.data.map((c, index) => {
                    if (index % 3 == 1)
                      return (
                        <ListGroupItem
                          key={index}
                          onClick={() => {
                            this.setState({ category: c.Title });
                          }}
                        >
                          {c.Title}
                        </ListGroupItem>
                      );
                  })}
                </ListGroup>
              </Col>
              <Col sm={12} md={4}>
                <ListGroup>
                  {this.state.data.map((c, index) => {
                    if (index % 3 == 2)
                      return (
                        <ListGroupItem
                          key={index}
                          onClick={() => {
                            this.setState({ category: c.Title });
                          }}
                        >
                          {c.Title}
                        </ListGroupItem>
                      );
                  })}
                </ListGroup>
              </Col>
            </Row>
          </Grid>
        </div>
      );
    } else {
      return (
        <div className="background-grey">
          <ContentProducts category={this.state.category} />
        </div>
      );
    }
  }
}

export default Category;
