import React, { Component } from "react";
import {
  Grid,
  Row,
  Col,
  Image,
  Jumbotron,
  Collapse,
  Button
} from "react-bootstrap";
import "./listcomponent.css";
class ListComponent extends Component {
  state = {
    open: false
  };
  render() {
    return (
      <div>
        <Col>
          <div className="card">
            <div className={this.props.color + " card-color"}>
              <h1 className="card-title">{this.props.title}</h1>
            </div>
            <h2 className="text-title">{this.props.title}</h2>
            <p className="text-items">{this.props.items}</p>
            <h4 className="text-sub-title">
              Address :<font className="text-items">{this.props.address}</font>
            </h4>
            <h4 className="text-sub-title">
              Phone :<font className="text-items">{this.props.phone}</font>
            </h4>
            <Collapse in={this.state.open}>
              <div>
                <h4 className="text-sub-title">
                  Working time :
                  <font className="text-items">{this.props.workingtime}</font>
                </h4>
                <p className="text-items">{this.props.description}</p>
              </div>
            </Collapse>
            <Button
              className={this.props.color + " more-button rounded"}
              onClick={() => this.setState({ open: !this.state.open })}
            >
              More...
            </Button>
          </div>
        </Col>
      </div>
    );
  }
}

export default ListComponent;
