import React, { Component } from "react";
import ListComponent from "./listcomponent";
import firebase from "./config/config.jsx";
import { Grid, Row, Col, Badge } from "react-bootstrap";

class Content extends Component {
  state = {
    product: []
  };

  constructor() {
    super();
    this.unsubscribe = null;
  }

  onProductUpdate = querySnapshot => {
    const product = this.state.product;
    querySnapshot.forEach(doc => {
      const {
        Title,
        Address,
        Description,
        Items,
        Phone,
        Workingtime
      } = doc.data();
      product.push({
        key: doc.id,
        doc, // DocumentSnapshot
        Title,
        Items,
        Address,
        Phone,
        Workingtime,
        Description
      });
    });
    this.setState({
      product: product
    });
  };

  componentWillMount() {
    const category = this.props.category;
    this.ref = firebase.firestore().collection(category);
    this.unsubscribe = this.ref.onSnapshot(this.onProductUpdate);
  }

  render() {
    let colors = [
      "red-card",
      "blue-card",
      "green-card",
      "yellow-card",
      "violet-card"
    ];
    let color;

    return (
      <div>
        <Grid>
          <Row className="show-grid containers containers-title">
            <h1 style={{ margin: 30 }}>
              <u>{this.props.category}</u>
              <Badge className="badge-margin">
                {this.state.product.length}
              </Badge>{" "}
            </h1>
          </Row>
          <Row>
            {this.state.product.map((c, index) => {
              color = colors[index % 5];
              return (
                <Col sm={12} sm={4} className="show-grid containers">
                  {" "}
                  <ListComponent
                    key={index}
                    color={color}
                    title={c.Title}
                    items={c.Items}
                    address={c.Address}
                    phone={c.Phone}
                    workingtime={c.Workingtime}
                    description={c.Description}
                  />
                </Col>
              );
            })}
          </Row>
        </Grid>
      </div>
    );
  }
}

export default Content;
